/**
 *  '2017/7/31'
 */
var getDayList=function(datestr,fill){
	/**
	 * ---,
	 * @param {Object} year
	 * @param {Object} month
	 */
	function getLastDaysInMonth(year,month){ 
	      var ldays0 = (new Date(year,month,0)).getDate();// ("1998/12/0");//"xxxx/xx/0"
	      month=='12'?month=1:month+=1
	      m2=month
	      var ldays1=(new Date(year,month,0)).getDate();
	      month=='12'?month=1:month+=1
	      m3=month
	      var ldays2=(new Date(year,month,0)).getDate();
	      var dayarr=[ldays0,ldays1,ldays2];//  
	      if(config_param.isLog)console.log(dayarr);
	      return dayarr;
	}
	var date,m2,m3;
	datestr?date=new Date(datestr):date=new Date();
//	var date=new Date('2017/7/31');
//	var date=new Date();
	var year=date.getFullYear(),m1=date.getMonth(),c_day=date.getDate(),wday=date.getDay();//  
	var days=getLastDaysInMonth(year,m1);
	
	var  clist={};
	var crr_m=[],next_m=[];//  
	for(;c_day<days[1]+1;c_day++){//
		crr_m.push(c_day);
	}
	clist[m2]=crr_m	
	
//	var all_days = 42-wday//
	var all_days = 35//
	var lastday=all_days-clist[m2].length-days[2]//
	var m2days;//
	if(lastday<0){//
		m2days=all_days-clist[m2].length+1
	}else{//
		m2days=days[2]+1
		var nextnext_m=[];
		for(var m=1;m<lastday+1;m++){//
			nextnext_m.push(m);
		}
		var m4=m3
		m4=='12'?m4=1:m4+=1;//  
		clist[m4]=nextnext_m;
	}
	for(var n1=1;n1<m2days;n1++){// 
		next_m.push(n1);
	}
	clist[m3]=next_m;
	
	if(fill){
		clist['0']=[];
		var n=date.getDay()
		for(var i=n;i>0;i--){
			clist['0'].push("");
		}
	}
	if(config_param.isLog)console.log(clist);
	return clist;
}


function getUrl (linkUrl) {
  var str, arr, newArr, num, num2, i, j, newJson
  if (linkUrl) {
    //str = decodeURIComponent(window.location.search.split('?')[1])
    str = decodeURIComponent (linkUrl.split('?')[1])
    arr = str.split('&')
    num = arr.length
    newJson = {}
    for (i = 0; i < num; i++) {
      newArr = arr[i].split('=')
      num2 = newArr.length
      newJson[newArr[0]] = newArr[1]
    }
    return newJson
  } else {
    return {}
  }
}

// ---JS
function requeryUsJS(src,cb) {
    var scriptEle = document.createElement("script");
    scriptEle.src = src ;
    document.head.appendChild(scriptEle);
    scriptEle.onload = function() {
        cb();
    }
}
