<template>
  <div class="Place">
    <div class='localbox '>
    	<div class="blink">{{hua_val}}</div>   <!-- -->
      <span :id="'top'+curCityId" ></span>
      <!----------------------------------------->
      <div class="localtitle clear mar1">
        <div class='local-l left'> <span v-if='checkCityName'>:&nbsp;{{checkCityName}}{{checkDistrict}}</span></div>
        <div class='local-r right'>  <i class='toup' @click="shouqi"></i></div>
      </div>
      <!--<div class='city_t'></div>-->
      <!--------------------------------------------------->
      <div class='localarea clear localarea1'>
        <a class='district firstChild left  district1 all c4e' @click='toList()'></a>
        <!--<a class='district left district1 c4e' v-for='(addr,index) in  districtArr' @click='checkDisc(addr,index)' :id='index'>{{addr.name}}</a>-->
        <!---->
        <a class='district left district1 c4e' v-for='(addr,index) in  districtArr' @click='checkDisc(addr.ckCityVo,addr.code)' :id='addr.code'>{{addr.ckCityVo.name}}</a>
      </div>
    </div>
    <!---------------------------------------------------------->
    <div class="cityListArea">
      <!-------------------------------------------------------->
      <div class="indexlist-bar">
        <a class='mar3'><span @click="curIndex(curid)"></span></a>
        <div class="slide" @touchend="touchEnd()" @touchmove="move($event)"> <!-- -->
        <a v-for='(str,id) in cityObj' v-if="JSON.stringify(str)!='{}'" ><span @click="letterIndex(id,$event)" class="fontColor" :class="{zimu : id==hua_val}">{{id}}</span></a>
        </div>
      </div>
      <!------------------------------------------------------------>
      <div class="cityListAZ">
        <ul class="cityItems">
          <li :id='index' v-for='(items,index) in cityObj'>
            <ol class="citiBlocks citiBlocks1">
              <li class="indexlist-index" v-if="JSON.stringify(items)!='{}'">{{index}}</li>
              <li class='cityli' v-for='(item,cityId) in items' @click='checkCity(item,cityId,index)' :id='cityId'>{{item.name}}</li>
            </ol>
            <div class="clear"></div>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script type="text/javascript">
  import $ from 'jquery'
  import { getUrl, requeryUsJS, hideaddress } from '../../../assets/js/BaseJS.js'
  import {config_param} from '../../../assets/js/config_param'
  import { getPosition } from '../../../assets/js/getLocation.js'
  import { Indicator, Toast, MessageBox } from 'mint-ui'
  import API from '../../../api/API'
  const api = new API();
  export default {
    name: 'Place',
    data() {
      return {
        curid: '', // id
        checkCityName: '', // 
        checkDistrict: '', // 
        localcity: '', // 
        districtArr: [], // 
        localcity: '', // 
        cityObj: {}, // 
        orderInfo: {districtCode: ''}, // 
        City: {}, //  
        willClick: false, // 
        backFlag: false, // 
        localFlag:false, // 
        curCityId:'',//id
        hua_val:'',//
        allCode:false,// 
        s_cityId:'',//sessionid
      }
    },
    beforeCreate() {
      document.title = ''
    },
    mounted() {
//    hideaddress();
      this.backFlag = JSON.parse(sessionStorage.getItem('backFlag')) || false // 
      this.fillCityPage();
    },
    methods: {
      fillCityPage:function(){
        var url = window.location.href; // 
        var linkUrl = getUrl(url); // JSON
        var orderInfo = JSON.parse(sessionStorage.getItem('orderInfo')); // orderInfosession
        if(orderInfo){
          this.orderInfo = $.extend({}, orderInfo, linkUrl);
        }else{
          sessionStorage.setItem('isCheckStore', true)
          orderInfo = sessionStorage.setItem('orderInfo', JSON.stringify(linkUrl)); // session
          orderInfo = JSON.parse(sessionStorage.getItem('orderInfo')); // orderInfosession
          this.orderInfo = orderInfo;
        }
          this.localFlag = orderInfo.localFlag?orderInfo.localFlag:false;
          this.willClick = orderInfo.willClick || false;
          var param = {"code": "","source":this.orderInfo.source?this.orderInfo.source:'' } // 
          this.fillCity(param); //   
          if(this.willClick == false) { //   
            this.fillLocation();
            this.location();
          }
        if(this.orderInfo.isToDetail || this.orderInfo.isSureCheck){
          this.orderInfo.isToDetail = false;
          this.orderInfo.isSureCheck = false;
          this.orderInfo.currentPage= 1;
        }
      },
      // 
      backFill: function() {
        if(this.backFlag == true) { // 
          this.checkCityName = this.orderInfo.cityName?this.orderInfo.cityName:" " // 
          this.checkDistrict = this.orderInfo.district?this.orderInfo.district:" " // 
          this.willClick = true; //   
          var cityN = this.orderInfo.cityName; // session
          var cityI = this.orderInfo.cityId; // sessionId
          this.s_cityId = cityI;
          var disId = this.orderInfo.districtCode // session
          this.allCode = this.orderInfo.allCode;
          sessionStorage.setItem('backFlag', false)
          // 
          if(this.orderInfo.cityId){
            this.fillDisc(cityI, disId) // 
          }
        }
      },
      // 
      fillLocation: function() {
        //  
        var qqlocation = JSON.parse(sessionStorage.getItem('ifGetLocation'));
        if(qqlocation && qqlocation.yesOrNo == 1) {
          var t = qqlocation.time;
          var outtime = new Date().getTime() - t > 600000 ? true : false; //10  10*60*1000 1800000
          if(outtime) { // 
            sessionStorage.removeItem('ifGetLocation'); // session
            this.location(); // 
          } else { // 
            this.backFill(); // 
            this.orderInfo.localFlag = true; // 
          }
        }
      },
      // 
      location: function() {
        Indicator.open({text: '', spinnerType: 'fading-circle'});
        var _this = this;
        var ifGetLocation = {};
        requeryUsJS('https://3gimg.qq.com/lightmap/components/geolocation/geolocation.min.js', function() {
          getPosition(
            function(data) {
              Indicator.close();
              if(_this.willClick) return
              _this.localcity = data.city; // 
//            _this.checkCityName = _this.localcity; // 
              _this.orderInfo.cityName = data.city; // session
              _this.orderInfo.latiude = data.lat; // session
              _this.orderInfo.longitude = data.lon; // session
              // 
              if(_this.localcity != "" && _this.willClick == false) { _this.findCity();}
              ifGetLocation = {"yesOrNo": 1,"data": data, "time": new Date().getTime()};
              sessionStorage.setItem('ifGetLocation', JSON.stringify(ifGetLocation)); // session
            },
            function() {
              Indicator.close();
              sessionStorage.setItem('ifGetLocation', JSON.stringify({"YoN": 2})); // session
            }
          )
        })
      },
      // 
      fillCity: function(param) {
        let _this = this;
        let response = api.ckcity(param); // 
        response.then(function(res) {
          if(res.data.state == '1') {
            _this.cityObj = res.data.data; // 
            _this.backFill(); // 
            //  id
            if(_this.backFlag == true||_this.localFlag == true) { 
              setTimeout(function() {$("#" + _this.orderInfo.cityId).addClass('cityChecked')}, 100);// 
              _this.curid = _this.orderInfo.curid // id
            }
          } else {
            if(res.data.Message) {
              Toast({ message: res.data.Message,duration: 1000,className: 'mToast'})
            }
          }
        }).catch(function(err) {
          if(config_param.isLog)console.log(err);
//        window.location.href = "#/common/errorPage"
        });
      },
      // 
      checkCity: function(item, cityId, indexId) {
        this.orderInfo.districtCode = '';
        this.checkDistrict = '';
        this.allCode = false;
        let _this = this;
        _this.willClick = true; //  
        _this.orderInfo.willClick = true; //  
        if(item.field == 1) {if(config_param.isLog)console.log("field--1") }
        if(item.field == "") {if(config_param.isLog)console.log('" "--') }
        _this.checkCityName = item.name; //  cityName
        _this.orderInfo.cityName = item.name; // session cityName
        _this.orderInfo.cityId = cityId; // sessionid
        _this.orderInfo.curid = indexId // 
        _this.curid = indexId;
        $('.localarea').show(); // 
        $('.toup').removeClass("todown"); // 
        $('.citiBlocks').find("li").removeClass('cityChecked');
        $("#" + cityId).addClass('cityChecked');
        this.fillDisc(cityId, "")
      },
      // 
      fillDisc: function(cityId, discId) {
        this.curCityId = cityId;
        let _this = this;
        var param = { "code": cityId,"source":_this.orderInfo.source?_this.orderInfo.source:''}
        let response = api.ckcity(param); // 
        response.then(function(res) {
          if(res.data.state == '1') {
            _this.districtArr = res.data.data;
            $('.district').removeClass('check');
            if(discId != "") {
              setTimeout(function() {$("#" + discId).addClass('check');}, 100);
            }else{
              setTimeout(function() {if(_this.allCode == true&&_this.s_cityId == cityId){$(".all").addClass('check');}}, 100);
            }
          } else {
            if(res.data.Message) {
              Toast({message: res.data.Message,duration: 1000,className: 'mToast'})
            }
          }
        }).catch(function(err) {
          if(config_param.isLog)console.log(err);
//        window.location.href = "#/common/errorPage"
        });
        // 
        setTimeout(function(){_this.topIndex('top'+cityId);},300)
        return false
      },
      // 
      checkDisc: function(disc, id) { // 
        this.checkDistrict = disc.name; //
        $(".district").removeClass('check'); // 
        $("#" + id).addClass('check'); // 
        this.orderInfo.district = this.checkDistrict; // session
        this.orderInfo.districtCode = id; // sessionid
        // 
        if(this.localFlag==false){this.orderInfo.localFlag = false;}
        this.orderInfo.willClick = true; // 
        var orderInfo = this.orderInfo; // orderInfosession
        sessionStorage.setItem('orderInfo', JSON.stringify(orderInfo)); // orderInfosession
        // 
        setTimeout(function() {window.location.href = "#/gx/order/checkStation"}, 100)
      },
      // 
      curIndex: function() {
        if(this.curid != "") {
          return document.getElementById(this.curid).scrollIntoView(true)
        }
      },
      // 
      letterIndex: function(curid,e) {
      	$('.blink').css({display:'block'});
        $('.blink').css({'opacity': 1})
        this.hua_val=$(e.target).text();
      	this.touchEnd();
		    $('.fontColor').removeClass('zimu');
		    $(e.target).addClass('zimu'); 
        return document.getElementById(curid).scrollIntoView(true); // 
      },
      topIndex: function(curid) {
        return document.getElementById(curid).scrollIntoView(true); // 
      },
      // 
      shouqi: function() {
        $(".localarea").animate({height: 'toggle'},300); // toggle
        $(".toup").toggleClass("todown"); // toggle
//      setTimeout(()=>{$(".cityListArea").toggleClass("city_top");},200)
        
        
      },
      // 
      toList: function() {
        if(this.checkCityName != "") {
          this.orderInfo.districtCode = ""; // 
          this.orderInfo.district = ""; // 
          // 
          if(this.localFlag==false){this.orderInfo.localFlag = false;}
          this.orderInfo.willClick = true; // 
		      this.orderInfo.allCode = true;
          sessionStorage.setItem('orderInfo', JSON.stringify(this.orderInfo))
          var place = JSON.parse(sessionStorage.getItem('orderInfo'));
          if(config_param.isLog)console.log('-----------------quancheng-------------')
          if(config_param.isLog)console.log(place)
          if(place.cityId) {
            setTimeout(function() { window.location.href = "#/gx/order/checkStation"}, 100)
          }else{
            Toast({message:''})
          }
        } else {
          Toast({ message: "",duration: 1500,className: 'mToast'})
        }
      },
      // 
      findCity: function() {
        var _this = this
        var val = _this.localcity; //
        var cityArr = _this.cityObj; // 
        var i, j, city;
        for(i in cityArr) {
          var c_a = cityArr[i]; // 
          for(j in c_a) {
            if(c_a[j].name == val) {
              _this.orderInfo.curid = i; //id
              _this.orderInfo.cityId = j; // id
              _this.orderInfo.cityName = val; // 
              _this.checkCityName = val; // 
              _this.orderInfo.districtCode = ""; // 
              _this.orderInfo.localFlag = true; // 
              _this.orderInfo.willClick = false; // 
            }
          }
        }
        sessionStorage.setItem('orderInfo', JSON.stringify(this.orderInfo)) // session
        var place = JSON.parse(sessionStorage.getItem('orderInfo'));
        // 
        if(_this.localFlag == false && place.cityId){ window.location.href = "#/gx/order/checkStation" }
      },
//    
      touchEnd(){
       	 var opcityNum = 1;
          setInterval(function () {
              opcityNum -= 0.1;
              if (opcityNum > 0) {
                  $('.blink').css({'opacity': opcityNum})
              } else {
                  clearInterval();
              }
          },50);
       setTimeout(function(){ $('.blink').css({display:'none'})},200);

      },
//    
      move(event){
       	event.preventDefault();
       	  $('.blink').css({display:'block'});
       	   $('.blink').css({'opacity': 1})
       	   var x=event.changedTouches[0].clientX;
       	   var y=event.changedTouches[0].clientY;
       	   	var xx=document.elementFromPoint(x,y);
       	   	 var word=$(xx).text();
       	   	 if(/^[A-Z]$/.test(word)){
       	   	 	this.hua_val=word;
       	   	 }
      		this.topIndex(this.hua_val);
        }
      },
      
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  @import '../../../assets/css/gx/order.css'
</style>


