<template>
	<div class="shopDetail">
		<img id="hidden_src" style="display:none;" src="../../../assets/images/user/onerror.png">
		<div class="shopDet">
			<div class="shop_banner">
				<mt-swipe :show-indicators="false">
					<!--<mt-swipe-item><img src="../../../assets/images/user/banner.png" alt="" /></mt-swipe-item>-->
				 <mt-swipe-item v-if="imgSrc"><img :src="imgSrc"></mt-swipe-item><!---->
				 <mt-swipe-item v-else><img src="../../../assets/images/user/menzhen.jpg" alt="" /></mt-swipe-item><!---->
				  
				  <!---->
				  <!--<mt-swipe-item><img src="../../../assets/images/user/banner.png" alt="" /></mt-swipe-item>
				  <mt-swipe-item><img src="../../../assets/images/user/banner.png" alt="" /></mt-swipe-item>-->
				  <!--<mt-swipe-item v-for = 'url in hosInfo.hospitalImgId' ><img :src="url" alt="" /></mt-swipe-item>-->
				</mt-swipe>
			</div>
			<div class="shop_name bgff">
				<p class="shop_n1">
					{{hosInfo.hospitalName}}
				<!--	//-->
				</p>
				<p class="shop_n2"> 
					<span class="shop_score" v-if="avgQuality !='--'">{{avgQuality}}<span><img v-if="true" v-for="n in parseInt(avgQuality)" src="../../../assets/images/user/star2.png" alt="" /><img  v-if="avgQuality.charAt(2)>'4'" src="../../../assets/images/user/helf.png" alt="" /><img v-if='true' v-for="n in 5-Math.round(parseFloat(avgQuality))" src="../../../assets/images/user/star1.png" alt="" /></span></span><span class="shop_score" v-if="avgQuality =='--'"><p class='gang'></p><p class='gang ganglast'></p><span><img  v-for="n in 5" src="../../../assets/images/user/star1.png" alt="" /></span></span>
				</p>
				
			</div>
			<p class="order_line"></p>
			<div class="shop_cell bgff">
				<div class="b_address b_address1">
						<p class="ad_txt posi_re">
							<img class="posi_ab" src="../../../assets/images/user/order_icons2.png" alt="" />
							{{hosInfo.hospitalAddr}}
							<!--E23A1B -->
						</p>
					<p class="dh_img"></p>
					<p class="shop_guide" @click="topage">
						<span class="daoh"></span><span class="shop_km" v-show="distance"><span v-if="distance<=100">{{distance}}</span><span v-else>&gt;100</span>km</span></p><!---->
					
				</div>
				<p class="line_p"></p>
				<div class="remind_container">
					<div class="b_date b_date01" >
						<p class="date_txt date_txt01">
							<img class="posi_ab" src="../../../assets/images/user/order_icons.png" alt="" />
							: <span>{{hosInfo.hospitalRule}}</span>
						</p>
					</div>
				    <div class="div_remind">
				      <div class="t_remind">
				      	<span class="triangle-up"></span><!---->
				      	1.30<br />2.
				      </div>
				    </div>
				    <span class="sp_remind" @click="remind()"></span>
				</div>
			</div>
			<p class="order_line"></p>
			<div class="shop_tap bgff">
				<span  class="shop_tap1 active">
					
				</span>
				<span class="shop_tap2">
					
				</span>			
			</div>
			<div id="shop_t1" class="eval_detail padd_shop1">
				<ul class="ser ser01">
					{{hosInfo.hospitalDes}}
					<!--<li  v-for="p in serInfo">{{p}}</li>-->				
				</ul>
			</div> 
			<div class="e_detail padd_shop1" id="shop_t2" >
				<div v-if='status == 1' style='-webkit-overflow-scrolling:touch;'><!--	-->	
				<!--loadMore-->
		        <!--<mt-loadmore :autoFill=false  :bottom-method="loadBottom" :bottom-all-loaded="allLoaded" ref="loadmore"  bottomPullText=''>-->
		          <div >
					<!--<div v-for = '(item,index) in items'>-->
						<div v-for = '(item,index) in hosVal'>
						<div class="area bgff"><!---->
							<div class="de_header">
							<img class="touxiang" src="../../../assets/images/user/touxiang01.jpg" v-if="item.sex=='F'" alt="" /> <!--//-->
							<img class="touxiang" src="../../../assets/images/user/touxiang02.jpg" v-else alt="" />
							<div class="de_left">
								<p class="de_name" v-if="item.name">{{item.name}}</p>
								<p class="de_name" v-else></p>
								<p class="de_num de_num01">
									<span></span><span v-if="item.avg_evaluate" class="pos_rel">
										<img v-if='true' v-for="n in parseInt(item.avg_evaluate)" src="../../../assets/images/user/star2.png" alt="" /><img  v-if="item.avg_evaluate.charAt(2)>'4'" src="../../../assets/images/user/helf.png" alt="" /><img v-if='true' v-for="n in 5-Math.round(parseFloat(item.avg_evaluate))" src="../../../assets/images/user/star1.png" alt="" />	
									</span>
									<span v-else class="pos_rel">
										<img v-for="n in 5" src="../../../assets/images/user/star1.png" alt="" />
									</span>
								</p>
							</div>
							<div class="de_right">
								{{(item.createTime).substr(0,10).replace(/\-/g, ".")}}   <!--//-->
							</div>
						</div>
						<div class="d_body padl">
						<div class="de_txt">
							{{item.content}}
						<!--	-->
						</div>
						<div class="debody_img" v-show="item.fileArray">
							<div v-for="(p,i) in item.fileArray"  class="img_over img_over01" :data-i='i'>
								<div v-show="p.fileResourceId" class="img_sre">
									<img  class="img_src"  :src="p.fillsrc">	
									<!--<img  class="img_src"  :src="p.fillsrc" @error="'javascript:this.src='+errImg+';'">-->	
								</div>
							</div>

						</div>
						</div>	
						</div>					
						<p class="order_line"></p>
					</div>
			          </div>
		        <!--</mt-loadmore>-->
				</div>	
				<div v-else class="area"></div><!---->
			</div>
		<!--	<p class="order_line"></p>-->
			<div class="shop_footer" @click="goOrder" v-show="isOrder">
				<p ></p>
			</div>
			
		</div>
		<!--<mt-popup v-model="popupVisible" position="middle" >-->
			<div class='pic_fix' style="display: none;">
				<!--<mt-swipe :show-indicators="true"  :auto="0"  @change="handleChange" >-->
				<mt-swipe :show-indicators="false"  :auto="0"  @change="handleChange" :defaultIndex='indexnow'>
			  	 <mt-swipe-item v-for='s in fillsrc' style='display:flex'>
			  	 	 <img :src="s" >
			  	 </mt-swipe-item>
				</mt-swipe>
				<div class="chahead">
					<div class="cha"></div>
					<div class='sw_index'>
						<span class="hindex"></span>/<span class="mintleng"></span>
					</div>
				</div>
				<img src="">
				<input type="hidden" v-model="fillsrc.length" class="hiddeninput">
			</div>
		<!--</mt-popup>-->
		<div class="remind_modal" style="display:none;">
		</div>
	</div>
</template>

<script>
	import $ from 'jquery';
	import {Toast,Swipe,SwipeItem,Indicator,MessageBox,Loadmore} from 'mint-ui';
	import {requeryUsJS} from '../../../assets/js/BaseJS.js'
	import {getPosition} from '../../../assets/js/getLocation.js'
	import { config_param } from '../../../assets/js/config_param';
	import API from '../../../api/API';
  	const api = new API();
	export default {
		data () {
			return{
				popupVisible:false,//
				distance:'',//
				avgQuality:'--',//
				hosInfo:{} ,       //
				hosVal:'' ,//
			 	serInfo:[], //
			 	imgSrc:'' , //
			 	isOrder:true,
			 	localMsg:'',//
			 	config_param:config_param,
			 	topStatus: '',// loadmore
		        allLoaded:false,// loadmore
		        pageIndex:0,// 
		        items:[],// 
		        results:[],// 
		        status:'',// 
		        avg_evaluate:'4.6',
		        fillsrc:[],// src
		        errImg:"/#/assets/images/user/onerror.png",
		        fillsrc:[],
		        hanindex:'',
		        mintleng:'',
		        indexnow:0
			}
		},
		beforeCreate () {document.title = '';sessionStorage.setItem('back_ticketURI','1');},
		beforeRouteLeave:(to, from, next) => {
			var that=this;
			var toMap=sessionStorage.getItem('toMap')||"";
	      	if(toMap)toMap=JSON.parse(toMap);
	      	if(toMap=='1'){
			      next(false);
	      	}
		    if(sessionStorage.getItem('back_ticketURI')){//
		      	var pic = $('.pic_fix').css("display");
		    	if(pic!='none'){// &&diswrap=='block'
					$('.pic_fix').hide();
//					$('.hiddeninput').val(0);
					next(false);
					sessionStorage.setItem('back_ticketURI','0');
					document.getElementById('meta').setAttribute('content',"width=device-width, initial-scale=1.0, user-scalable=no, minimum-scale=1.0, maximum-scale=1.0");
				}else{
			            next();
				}
		    }else{
		        next();
		    }
	    },
		mounted () {
			this.addActive();//
			this.addTap();
			this.locationSession();//
	    	this.init();
	    	this.showBigPic();
	    	
	    },
	    methods: {
	    	handleChange:function(index){//
	    		this.hanindex = index+1
//	    		console.log('this.hanindex:'+this.hanindex);
	    		$('.hindex').text(this.hanindex)
	    	},
	    	remind(){
	    		$(".remind_modal").css('display','block');
	    		$(".div_remind").css('display','block');
	    		//
				$('.remind_modal').click(function(){
					$('.remind_modal').hide();
					$(".div_remind").hide();
				});
	    	},
	    	init:function(){
	    		var orderInfo = sessionStorage.getItem('orderInfo');
	    		orderInfo = JSON.parse(orderInfo);
	    		//console.log(orderInfo);
    		  	this.orderInfo = orderInfo; //  ordersession
    		  	var doctorId = orderInfo.doctorId;  //Id
    		  	var hospitalId = orderInfo.hospitalId;  //Id		
					if(orderInfo.ifOrder){
						this.isOrder=false;      //
					}	    		
				if(orderInfo.distance){this.distance= orderInfo.distance};  //										 
				var param={					
					  "retrieveArgs": {
					    "currentPage": "1",
					    "hospitalId":hospitalId,
					    "hospitalName": "",
					    "latiude": "",
					    "longitude": "",
					    "provCode": "",
					    "rowsPerPage": "1"
					  }
				}
				var param_v={"doctorId":doctorId,"orgId":hospitalId}
		     	this.fillInfo(param);		 //    	
		     	this.fillValue(param_v);     //
		     	this.errImg=$('#hidden_src').attr('src')
//		     	console.log(this.errImg)
		     	
	    	},
	    	addActive:function(){
	    		$("#shop_t2").css("display","none");
				$("#shop_t1").css("display","block");
	    	},
	    	addTap:function(){
				$(".shop_tap1").click(function(){
					$(this).addClass("active");
					$(".shop_tap2").removeClass("active");
					$("#shop_t1").css("display","block");
					$("#shop_t2").css("display","none");
				})
				$(".shop_tap2").click(function(){
					$(this).addClass("active");
					$(".shop_tap1").removeClass("active");
					$("#shop_t2").css("display","block");
					$("#shop_t1").css("display","none");
				})
	    	},
	    	//
	    	showBigPic(){
	    		var that=this;
				$('#shop_t2').on('click','.debody_img img',function(){//
//						that.indexnow=0;
						that.indexnow=parseInt($(this).parents('.img_over').attr('data-i'));
//						console.log(that.indexnow+',id');
						that.fillsrc=[];
					//console.log('that.indexnow'+that.indexnow);
//					$('.pic_fix img').attr('src',$(this).attr('src'));
					$('.pic_fix').show();
					var imgs=$(this).parents('.debody_img').find('img');
					console.log(imgs.length);
					var len=imgs.length;
					var i=0;
					imgs.each(function(){
						i++;
						var src=$(this).attr('src')
						that.fillsrc.push(src);
					});
					if(i==len){
						setTimeout(function(){
							that.mintleng = $('.mint-swipe-item').length;//
//							console.log($('.mint-swipe-item').length);
							var mintwarp = $('.mint-swipe-item.is-active').index();//
//							console.log('that.hanindex:'+that.hanindex);
							if(!that.hanindex){//chage
								$('.hindex').text(mintwarp+1)
							}
							$('.mintleng').text(that.mintleng-1);	
						},100);
						
					}
					
//					that.popupVisible=true;
					document.getElementById('meta').setAttribute('content',"width=device-width, initial-scale=1.0, user-scalable=yes, minimum-scale=1.0, maximum-scale=5.0");
				})
				//
				$('.cha').click(function(){
					$('.pic_fix').hide();
					that.fillsrc=[];
//					that.popupVisible=false;
					document.getElementById('meta').setAttribute('content',"width=device-width, initial-scale=1.0, user-scalable=no, minimum-scale=1.0, maximum-scale=1.0");
				});
	    	},
    	  // 
		  fillInfo (param) {
		  	var that=this;
	        let response = api.getOrgListBySolr (param); // ,
	        response.then(function(res){
	          var data=res.data;
//	          console.log(data);
	          if(data.status=='1'){
	          	var dataInfo=data.hospitalsBySolrResponseVoList[0];
	            that.hosInfo=dataInfo;
	            	setTimeout(function(){
	            		var h=$('.b_address').height();
	    				$('.dh_img').height(h);
	            	},10)
	            if(dataInfo.distance){that.distance= dataInfo.distance};//
	            if(dataInfo.avgQuality){that.avgQuality= dataInfo.avgQuality};  //	
        //
        		if(!(dataInfo.hospitalImgId&&dataInfo.hospitalId)){return;}
	            var pictureId=dataInfo.hospitalImgId;	
	            var hospitalId=dataInfo.hospitalId;		            
	            var p={"hospitalId":hospitalId,"pictureId":pictureId};
	            //console.log("+++++++++");
	            //console.log(p);	            	        		
		        	let response = api.getPicture(p); // 
	              	response.then(function(res){
			          var data=res.data;
			          //console.log(data);
			          if(data.status=='1'){
			         	that.imgSrc=data.picture;	
			          }else{
			            if(data.message){  // Toast
			              console.log(data.message);
			            }else{
		              	console.log('');
			            }
			          }
			        })
			        .catch(function(err){
//			        	window.location.replace("#/common/errorPage");
			        });
			        
	          }else{
	          	window.location.replace("#/common/errorPage");//
	            if(data.message){  // Toast
              	console.log(data.message);
	            }else{
	              console.log('');
	            }
	          }
	        })
	        .catch(function(err){
//	        	window.location.replace("#/common/errorPage");//
	        });
	      },
    	  // 
		  fillValue (param_v) {
		  	var that=this;
		  	that.allLoaded = false;
        	that.pageIndex = 0;
		  	//console.log(param_v);
	        let response = api.selectValuation(param_v); // 
	        response.then(function(res){
	          var data=res.data;
//	          console.log(data);
	          that.status = data.status; // 
	          if(data.status=='1'){
//	            that.hosVal=data.selectValuationResponseVoList;
	            var allArr = data.selectValuationResponseVoList
	            var deimg_w,deimg_h;
	            for(var i=0;i<allArr.length;i++){
	              var imgobj = allArr[i].fileArray;
	              if(imgobj !=null){
	                for(var j=0;j<imgobj.length;j++){
                  	imgobj[j].fileResourceId?that.fileId = imgobj[j].fileResourceId:allArr[i].fileArray[j]=[];
                    if(that.fileId !=null){
                      var src_url = config_param.base_path+'/webApp/ecm/common/'+that.fileId+'/default'
                      allArr[i].fileArray[j].fillsrc=src_url;
                      var deimg = new Image();
                      deimg.src = src_url;
                      var src="";
//                    deimg.onerror=function(){
//                    	console.log(that.errImg);
//                    	deimg.src=that.errImg;
//                    	allArr[i].fileArray[j].fillsrc=that.errImg;
//                    }
                      deimg.onload = function(){
                      		  var out_hei = $('.img_sre').height();
							  var out_wid = $('.img_sre').width()
                              $('#shop_t2 .img_over img').css({'max-width':'none'});
                              $.each( $('#shop_t2 .img_over img'), function(i, n){
                                deimg_w = n.naturalWidth;//naturalWidth
                                deimg_h = n.naturalHeight;//naturalHeight
                                if(deimg_w > deimg_h){
			                         n.style.width = 'auto';n.style.height = '100%';
			                        var img_wh = deimg_h/out_hei;
			                        var dewidth = deimg_w/img_wh;
			                        var dewh = '-'+(dewidth-out_wid)/2+'px';
			                        n.style.left = dewh
			                    }else{
			                        n.style.width = '100%';n.style.height = 'auto'
			                        var img_wh = deimg_w/out_wid
			                        var deheight = deimg_h/img_wh;
			                        var dewh = '-'+(deheight-out_hei)/2+'px';
			                        n.style.top = dewh
			                    }
                              });
                      };
                      
//                    deimg.onerror = function(){//
//                    	src_url=that.errImg;
//                    }
//                    deimg.oncomplete=function(){
//                    	allArr[i].fileArray[j].fillsrc=src_url;
////                    }
                    }
                  }
	              }
	              
	            }
	            that.hosVal = allArr;
	            that.results = that.sliceArr(that.hosVal, 10) // 
            	that.items= that.results[0]; // 	         		
	          }else{
	            if(data.message){  // Toast
	              console.log(data.message);
	            }else{
	              console.log('');
	            }
	          }
	        })
	        .catch(function(err){
//	        	window.location.replace("#/common/errorPage");//
	        });
	      },
	      // 
	      sliceArr:function(array, size){
	        var result = [];
	        for (var x = 0; x < Math.ceil(array.length / size); x++) {
	          var start = x * size;
	          var end = start + size;
	          result.push(array.slice(start, end));
	        }
	        return result;
	      },
	      // 
//	      loadBottom:function(){
//	        if(this.results.length ===1){// 
//	          Toast({message:'',duration: 1000});
//	        }else{
//	          if(this.pageIndex == this.results.length-1){
//	          	this.allLoaded = true;
//	          	Toast({message:'',duration: 1000});
//	          }else{
//	          	//this.items.push.apply(this.items,this.results[++this.pageIndex]);
//		         this.items = (this.items).concat(this.results[++this.pageIndex]);
//	          }	          
//	        }
//	        this.$refs.loadmore.onBottomLoaded();
//	      },
	      goOrder:function(){
	      	sessionStorage.setItem('back_ticketURI','0');
	      	sessionStorage.setItem('toMap','0');
		      window.location.href = "#/gx/order/toOrder"
		    },
	    locationSession:function(){ 
		  	//  
	    	var qqlocation=JSON.parse(sessionStorage.getItem('ifGetLocation'));
	    	if(qqlocation&&qqlocation.yesOrNo==1){
	    		var t=qqlocation.time;
	      	var outtime=new Date().getTime()-t>600000?true:false; //10  10*60*1000 1800000
	      	if(outtime){//session
	  				sessionStorage.removeItem('ifGetLocation');
	  				this.location();
	      	}else{//session
				this.localMsg = JSON.parse(sessionStorage.getItem('ifGetLocation')).data;
	      	}
	    	}
	    },
        // 
	    location:function(){	      
	      var _this = this;
	      var ifGetLocation = {}; 
	      requeryUsJS('https://3gimg.qq.com/lightmap/components/geolocation/geolocation.min.js',function(){
	         getPosition(
	          function(data){
	           console.log("--------------------------");
	            _this.localMsg = data;
	            _this.orderInfo.localFlag = true;
	           ifGetLocation={"yesOrNo":1,"data":data,"time":new Date().getTime()}
	           sessionStorage.setItem('ifGetLocation',JSON.stringify(ifGetLocation));
	         },
	         function(){
	          console.log("----------");
	          sessionStorage.setItem('ifGetLocation',JSON.stringify({"YoN":2}));
	         }
	         )
	      })
	    },
		 topage:function(){  //
	    	var start={"lat":this.localMsg.lat,"lon":this.localMsg.lon,"addr":""};
	//		    	alert(JSON.stringify(start));
	    	var end=this.hosInfo.hospitalLongitudeLatitude.split(",");
	    	var x=end[0],y=end[1];
	    	var dest={"eword":this.hosInfo.hospitalName,"epointx":x,"epointy":y}
	    	var addr=this.hosInfo.hospitalAddr;
			sessionStorage.setItem('back_ticketURI','0');
    		var url = "http://apis.map.qq.com/tools/poimarker?type=0&marker=coord:" + dest.epointy + "," + dest.epointx + ";title:" + encodeURIComponent(dest.eword) + ";addr:" + encodeURIComponent(addr) + "&key=OB4BZ-D4W3U-B7VVO-4PJWW-6TKDJ-WPB77&referer=myapp"
	     	var u = navigator.userAgent;
	      	if (u.indexOf('iPhone') > -1) {//
		    	sessionStorage.setItem('toMap','1');
		    	window.location.href=url
		    }else{
		    	sessionStorage.setItem('mapRouter',JSON.stringify({'toMap':'1','toMapURI':url,'backURI':window.location.href}));
		    }
	  }
	    }
	}
</script>

<style>
  @import '../../../assets/css/gx/orderPro.css';

</style>


