<template>
  <div class="checkStation" :style="{'-webkit-overflow-scrolling':scrollMode}">
    <!---->
    <!-------------------------------->
    <div class='stationbox  clear searchFix' id='showSearchBox'>
      <span class='checkCity left' @click='backPlace()' v-if='isFoucus==false'>{{showCity}}</span>
      <div class='searchbox left'>
        <div class="inputbox">
          <i class='searchicon'></i>
          <input type="search" id="searchtext" v-model="searchWord" @focus="searchFoucus()" @keyup.enter='searchHospital()' placeholder="" />
          <a class='cancelbtn' @click='searchHospital()' v-if='searchWord'></a>
          <a class='cancelbtn' @click='searchHospital()' v-else-if="searchWord==''"></a>
        </div>
      </div>
    </div>
    <!---------------------------------->
    <div class='modal'>
      <div class='histrybox'>
        <h2 class='histitle'><i class='hisicon'></i></h2>
        <ul class="searchList">
          <li><span class='keywords' v-for='item in histryWord' @click='useHisWord(item)'>{{item}}</span></li>
        </ul>
        <div @click='clearHistory' class='clearbtn'></div>
      </div>
    </div>
    <!------------------------------------------>
    <div class='radioList padtop' >
      <!--<mt-loadmore  :autoFill="false" :top-method="loadTop" :bottom-method="loadBottom" :bottom-all-loaded="allLoaded" ref="loadmore" @top-status-change="handleTopChange" @bottom-status-change="handleBottomChange">-->
      <load-more    :bottom-method="loadBottom"    :bottom-all-loaded="allLoaded"    :bottomPullText='bottomText'    :auto-fill="false"    @bottom-status-change="handleBottomChange"    ref="loadmore">
      <ul >
        <li class="stationli clear" v-for='(hosp,index) in hospitalInfo' :id='hosp.hospitalId'>
          <i class='radiobtn left' @click="shureCheck(index,hosp)" :id='index'></i>
          <div class='storeInfo left' @click='toshopDetail(index,hosp)'>
            <h2>{{hosp.hospitalName}}</h2>
            <p class='store Addr'>{{hosp.hospitalAddr}}</p>
            <p class='store Fen'>&nbsp;<span class='fNum' v-show="hosp.avgQuality !='--'">{{hosp.avgQuality}}</span><span v-show="hosp.avgQuality =='--'" ><p class='hen'>&nbsp;</p><p class='hen'>&nbsp;</p></span></p><!--class='heng'<i class='h_mar'>-</i><i>-</i>-->
          </div>
          <div class='nearbox' v-if='index==0 && hosp.distance'><span v-show='index==0' class='near'  v-if='hosp.distance'></span></div>
          <div class='nearbox' @click='toshopDetail(index,hosp)' v-else><span v-show='index==0' class='near' v-if='hosp.distance'></span></div>
          <div class='navbox left' @click="gotoMap(hosp)">
            <div class='p_distance' >
              <p  class='distance' ><span v-if='hosp.distance'><span class='disNum' v-if="hosp.distance >'100'">&gt;100km</span><span class='disNum' v-show="hosp.distance < '100'">{{hosp.distance?hosp.distance:'0.0'}}km</span></span></p>
              <p class="navicon"><img src="../../assets/images/order/order_nav.png" alt="" /></p>
            </div>
          </div>
        </li>

      </ul>
      <!--<div slot="top" class="mint-loadmore-top">
        <span v-show="topStatus !== 'loading'" :class="{ 'rotate': topStatus === 'drop' }"><mt-spinner :size='20' color="#26a2ff"type="fading-circle"></mt-spinner></span>
        <span v-show="topStatus === 'loading'">Loading...</span>
      </div>
      <div slot = 'bottom'class="mint-loadmore-bottom"><span class="mint-loadmore-text"></span></div>-->
      <div v-show="loading" slot="bottom" class="loading"> 
        <!--divgif-->
        <mt-spinner :size='25' color="#26a2ff" type="fading-circle"></mt-spinner>
      </div>
      </load-more>
      <!--</mt-loadmore>-->
    </div>
    <mt-popup v-model="isCall" position="middle" :modal='false'>
      <div class='call-box'>
          <div class="call-title-tel"></div>
          <div class="call-text-tel"></div>
          <div class="call-btn-container">
            <!--<a :href="callAct"  class="call-btn-tel" @click="telphone()"></a>-->
            <a id="callbtn" class="call-btn-tel" @click="telphone()"></a>
            <span  class="call-btn-tel" @click="closeCall()"></span>
          </div>

      </div>
    </mt-popup >
    <div @click='closeCall()' class="call_modal" v-if='isCall'style="z-index: 1000;"></div>
    
    <!-------------------------------------------------------->
    <div class='whiteList'><img src="../../assets/images/order/searchbg.png" /><span class='whitetext'></span></div>
  </div>
</template>

<script type="text/javascript">
  import $ from 'jquery'
  import { getUrl, requeryUsJS, hideaddress } from '../../assets/js/BaseJS.js'
  import {config_param} from '../../assets/js/config_param'
//import LoadMore from './../loadmore/loadmore.vue';
  import { getPosition } from '../../assets/js/getLocation.js'
  import { Spinner,Toast, Loadmore, MessageBox } from 'mint-ui';
  import API from '../../api/API'
  const api = new API();
  export default {
    name: 'checkStation',
     props:{
    
        },
    data() {
      return {
        showCity: '', // 
        cityName: '', // 
        searchWord: '', // 
        searchFlag: '', // 
        orderInfo: {}, // 
        searchInfo: {}, // 
        hospitalInfo: [], // 
        histryWord: [], // 
        currentPage: 1, // 
        topStatus: '', // loadmore
        allLoaded: false, // loadmore
        localMsg: {}, // 
        isCall: false, // 
        callAct:'',//
        isFoucus: false,
        isToDetail:false,// 
        isSureCheck:false,// 
        scrollMode:'auto', //touchauto
        //  
                scrollHeight: 0,
                scrollTop: 0,
                containerHeight: 0,
                loading: false,
                allLoaded: false,
                bottomText: '...',
                bottomStatus: '',
                pageNo: 1,
                totalCount: '',
      }
    },
    beforeCreate() {
      document.title = '';$('#app,body').css({'height':'auto'})
    },
    created() {
      sessionStorage.setItem('backFlag', true)
      this.fillHospitalPage(); // 
      this.fillLocation();
    },
    destroyed(){$('#app,body').css({'height':'100%'})},
    mounted() {
      this.anios();
    },
    watch: {
      isCall: function(newVal, oldVal) {
        if (newVal === true) {
          $('#app,body').css({'height':'100%'})
        }
      }
    },
    methods: {
       /*  */
        _scroll: function(ev) {
            ev = ev || event;
            this.scrollHeight = this.$refs.innerScroll.scrollHeight;
            this.scrollTop = this.$refs.innerScroll.scrollTop;
            this.containerHeight = this.$refs.innerScroll.offsetHeight;
        },
      handleBottomChange(status) {
        this.bottomStatus = status;
      },
      telphone(){
        var _this=this;
        var param = {
//                  "channelType":sessionStorage.getItem('channelType')||'',
                "cityCode":_this.orderInfo.cityId,
                "cityName":_this.cityName,
                "policyNo":_this.orderInfo.policyNo,
                "wesurePolicyNo":_this.orderInfo.wesurePolicyNo,
                "phone":'4000895522'
              }
        api.phoneConsultInfo(param); // 
        var call_num = '4000895522';
        _this.isCall = true;
        _this.callAct = 'tel:'+call_num;
        window.open(_this.callAct);
      },
      // 
      closeCall:function(){
        this.isCall = false;
        window.location.replace( "#/order/Place");
      },
      anios: function() { //
//      $(window).scroll(function(){$('.searchFix').css({'position':'fixed'});})
        var u = navigator.userAgent;
        var isAndroid = u.indexOf('Android') > -1 || u.indexOf('Adr') > -1; //android
        var isiOS = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/); //ios
        if(isAndroid){
          $('.checkStation').css({'position':'absolute','bottom': '0','left':'0'})
        }
        if(isiOS) { // ios
          $(".near").css({'line-height':'0.83rem'});
        }
      },
      // 
      fillHospitalPage: function() {
        var url = window.location.href; // 
        var linkUrl = getUrl(url); // JSON
        var orderInfo = sessionStorage.getItem('orderInfo');
        orderInfo = JSON.parse(orderInfo);
        this.orderInfo = orderInfo; //  ordersession
        this.orderInfo = $.extend({}, orderInfo, linkUrl);
        this.cityName = orderInfo.cityName;
        if(orderInfo.cityName.length > 3) {
          this.showCity = orderInfo.cityName.slice(0, 3);
        } else {
          this.showCity = orderInfo.cityName;
        }
        this.isToDetail = orderInfo.isToDetail?orderInfo.isToDetail:false;
        this.isSureCheck = orderInfo.isSureCheck?orderInfo.isSureCheck:false;
        this.searchFlag = orderInfo.searchFlag?orderInfo.searchFlag:false;
        if(this.orderInfo.isToDetail || this.orderInfo.isSureCheck){
          this.currentPage = orderInfo.currentPage ? orderInfo.currentPage : 1;
        }
        var param = {
          "retrieveArgs": {
            "cityCode": this.orderInfo.cityId||'110000',
            "currentPage": "1",
            "districtCode": this.orderInfo.districtCode || "",
            "hospitalName": "",
            "latiude": this.orderInfo.latiude || "", // , lat: 39.90403
            "longitude": this.orderInfo.longitude || "", //  116.407526
            "provCode": "",
            "rowsPerPage": "",
            "source":this.orderInfo.source?this.orderInfo.source:''
          }
        }
        if(this.orderInfo.localFlag == true) {
          this.localMsg = JSON.parse(sessionStorage.getItem('ifGetLocation')).data;
          param.retrieveArgs.rowsPerPage = "300"
//        param.retrieveArgs.rowsPerPage = "10"
        }
        if(this.orderInfo.localFlag == false) {
          if(this.orderInfo.currentPage){
            param.retrieveArgs.currentPage = this.orderInfo.currentPage
          if(this.searchFlag==true){
            param.retrieveArgs.hospitalName = this.orderInfo.searchWord
          }
            
            this.orderInfo.currentPage = ''
            this.orderInfo.searchWord = ''
            sessionStorage.setItem('orderInfo', JSON.stringify(this.orderInfo))
          }
          param.retrieveArgs.rowsPerPage = "10"
//        param.retrieveArgs.rowsPerPage = "300"
          //      if(this.orderInfo.districtCode==""){
          //        param.retrieveArgs.rowsPerPage = "300"
          //      }
        }
        this.HospitalList(param, false);
      },
      // Place
      backPlace: function() {
        window.location.href = "#/order/Place"
      },
      // 
      HospitalList: function(param, search) {
        this.searchFlag = search;
        this.allLoaded = false;
        $(".whiteList").hide();
        let _this = this;
        let response = api.getOrgListBySolr(param); // 
        response.then(function(res) {
//        res.data.status=1
          if(res.data.status == '1') {
            var list = res.data.hospitalsBySolrResponseVoList; // 
//          var list = [{"hospitalAddr":"6","districtCode":"110101","spellNo":"AKCKBJBHZSKZBFY","provCode":"110000","lastModify":null,"distance":1,"cityCode":"110000","hospitalRule":"8:00-17:00","hospitalLongitudeLatitude":"116.4109800000,39.8925600000","hospitalName":"","avgQuality":null,"contactTel":"010-67016280609","parentId":"6249","hospitalDes":" 20141020100","hospitalId":"6254","createTime":null,"doctorId":"230253","orgSource":"0","orgCode":null,"hospitalImgId":null}]
            var len = list.length;
            if(config_param.isLog)console.log(list)
//                    var list = [{"hospitalAddr":"6","districtCode":"110101","spellNo":"AKCKBJBHZSKZBFY","provCode":"110000","lastModify":null,"distance":1,"cityCode":"110000","hospitalRule":"8:00-17:00","hospitalLongitudeLatitude":"116.4109800000,39.8925600000","hospitalName":"","avgQuality":null,"contactTel":"010-67016280609","parentId":"6249","hospitalDes":" 20141020100","hospitalId":"6254","createTime":null,"doctorId":"230253","orgSource":"0","orgCode":null,"hospitalImgId":null}]
            if(search == true) {
              _this.hospitalInfo = [];
              _this.orderInfo.searchFlag = true;
            } else {
              _this.orderInfo.searchFlag = false;
            }
            _this.hospitalInfo = list;
//          if(_this.hospitalInfo == []) {// 
//            _this.hospitalInfo = list;
//          } else {// 
//            _this.hospitalInfo = (_this.hospitalInfo).concat(list); // 
//          }
//          _this.$nextTick(function () {  
              // DOMDOM  
              // iphone-webkit-overflow-scrollingloadmore  
              // auto  
//            _this.loading = false; 
//          });
            if(_this.isToDetail || _this.isSureCheck){
              if(config_param.isLog)console.log(_this.isToDetail,_this.isSureCheck,_this.orderInfo.hospitalId)
              if(_this.orderInfo.hospitalId){
                setTimeout(function(){
                  if(_this.isSureCheck){
                    $("#"+_this.orderInfo.hospitalId).find('.radiobtn').addClass('checkRadio');
                  }
                  if(_this.currentPage==1 && len>10){
                    var h_id =$("#"+_this.orderInfo.hospitalId)[0]
                    $("html, body").animate({scrollTop:h_id.offsetTop-40}, { duration: 500, easing: "swing" })
                  }
                return document.getElementById('showSearchBox').scrollIntoView(true)
                },100);
              
              }
            }
          } else {
            if(res.data.message) {
              if(search == true) {
                _this.hospitalInfo = {}
                if(res.data.status == -1) {
                  $(".whiteList").show();
                  var param = {
//                  "channelType":sessionStorage.getItem('channelType')||'',
                    "cityCode":_this.orderInfo.cityId,
                    "cityName":_this.cityName,
                    "policyNo":_this.orderInfo.policyNo,
                    "wesurePolicyNo":_this.orderInfo.wesurePolicyNo
                  }
                  api.locationInfo(param); // 
                }
              } else {
                if(res.data.status == -1) {
                  if(_this.currentPage == 1) {
                    var call_num = '4000895522'
                    _this.isCall = true;
                    _this.callAct = 'tel:'+call_num;
                    var param = {
//                  "channelType":sessionStorage.getItem('channelType')||'',
                    "cityCode":_this.orderInfo.cityId,
                    "cityName":_this.cityName,
                    "policyNo":_this.orderInfo.policyNo,
                    "wesurePolicyNo":_this.orderInfo.wesurePolicyNo,
                    "phone":'4000895522'
                    }
                    api.phoneConsultInfo(param); //           
                  }else{
//                  _this.loading = false;
//                  _this.allLoaded = true;
//                  Toast({message: '',duration: 1500,className: 'mToast'})
                  }
                } else {
                  Toast({message: res.data.message,duration: 1500,className: 'mToast'})
                }
              }
            } else {
              Toast({message: "",duration: 1500,className: 'mToast'})
            }

          }
        }).catch(function(err) {
          if(config_param.isLog)console.log(err);
//        window.location.replace("#/common/errorPage");
        });
      },
      // 
      handleTopChange(status) {
        this.topStatus = status;
      },
      // 
      loadTop: function() {
        this.$refs.loadmore.onTopLoaded();
      },
      // 
      loadBottom: function() {
        this.loading = true;
        this.isPullup = true;
        var param = {
          "retrieveArgs": {
            "cityCode": this.orderInfo.cityId||'110000',
            "currentPage": ++this.currentPage,
            "districtCode": this.orderInfo.districtCode,
            "hospitalName": this.searchWord,
            "latiude": this.orderInfo.latiude || "", // , lat: 39.90403
            "longitude": this.orderInfo.longitude || "", //  116.407526
            "provCode": "",
            "rowsPerPage": "10",
            "source":this.orderInfo.source?this.orderInfo.source:''
          }
        }
        if(this.searchFlag == true) {
          param.retrieveArgs.rowsPerPage = "10"
//        param.retrieveArgs.rowsPerPage = "300"
        }
        if(this.searchFlag == false) {
          if(this.orderInfo.localFlag == true) {
            param.retrieveArgs.rowsPerPage = "300"
          }
          if(this.orderInfo.localFlag == false) {
            param.retrieveArgs.rowsPerPage = "10"
//          param.retrieveArgs.rowsPerPage = "300"
          }
        }
        
        let _this = this;
        setTimeout(()=>{
//        _this.HospitalList(param, false);
          let response = api.getOrgListBySolr(param); // 
          response.then(function(res) {
            if(res.data.status == '1') {
              var list = res.data.hospitalsBySolrResponseVoList; // 
              if(config_param.isLog)console.log(list)
              var len = list.length
              if(len<10 && _this.currentPage == 1){
                _this.allLoaded = true
              }
              _this.hospitalInfo = (_this.hospitalInfo).concat(list); // 
              _this.$nextTick(function () {  
                // DOMDOM  
                // iphone-webkit-overflow-scrollingloadmore  
                // auto  
                _this.loading = false; 
              });
            } else {
              if(res.data.message) {
                  if(res.data.status == -1) {
                    if(_this.currentPage != 1){
                      Toast({message: '',duration: 1500,className: 'mToast'})
                      _this.allLoaded = true;
                      _this.loading = false;
                    }
                  } else {
                    Toast({message: res.data.message,duration: 1500,className: 'mToast'})
                  }
              } else {
                Toast({message: "",duration: 1500,className: 'mToast'})
              }
  
            }
          }).catch(function(err) {
            if(config_param.isLog)console.log(err);
  //        window.location.replace("#/common/errorPage");
          });
          _this.$refs.loadmore.onBottomLoaded();
        },500)
        
      },
      // 
      fillLocation: function() {
        //  
        var qqlocation = JSON.parse(sessionStorage.getItem('ifGetLocation'));
        if(qqlocation && qqlocation.yesOrNo == 1) {
          var t = qqlocation.time;
          var outtime = new Date().getTime() - t > 600000 ? true : false; //10  10*60*1000 1800000
          if(outtime) {
            sessionStorage.removeItem('ifGetLocation');
            this.location();
          } else {
            this.orderInfo.localFlag = true;
            this.localMsg = JSON.parse(sessionStorage.getItem('ifGetLocation')).data
          }
        }
      },
      // 
      location: function() {
        var _this = this;
        var ifGetLocation = {};
        requeryUsJS('https://3gimg.qq.com/lightmap/components/geolocation/geolocation.min.js', function() {
          getPosition(
            function(data) {
              if(config_param.isLog)console.log("--------------------------");
              //            alert(JSON.stringify(data));//{lon: 116.407526, lat: 39 90403, city: ""}
              if(config_param.isLog)console.log(data.city);
              //            alert("lon"+data.lon+"   lat"+data.lat);
              _this.localMsg = data;
              _this.orderInfo.localFlag = true;
              ifGetLocation = {
                "yesOrNo": 1,
                "data": data,
                "time": new Date().getTime()
              }
              sessionStorage.setItem('ifGetLocation', JSON.stringify(ifGetLocation));
            },
            function() {
              if(config_param.isLog)console.log("----------");
              sessionStorage.setItem('ifGetLocation', JSON.stringify({
                "YoN": 2
              }));
            }
          )
        })
      },
      // 
      shureCheck: function(cur, item) {
        if(config_param.isLog)console.log(item);

        var id = cur;
        var lon = item.hospitalLongitudeLatitude.split(",")[0];
        var lat = item.hospitalLongitudeLatitude.split(",")[1];
        this.orderInfo.doctorId = item.doctorId;
        this.orderInfo.hospitalId = item.hospitalId;
        this.orderInfo.hospitalName = item.hospitalName;
        this.orderInfo.contactTel = item.contactTel;
        this.orderInfo.orgAddress = item.hospitalAddr;
        this.orderInfo.lon = lon;
        this.orderInfo.lat = lat;
        this.orderInfo.orgSource = item.orgSource; // 
        this.orderInfo.orgCode = item.orgCode; // id
        this.orderInfo.orderTime = {}
        this.orderInfo.isSureCheck = true;
        this.orderInfo.isToDetail = false;
        this.orderInfo.currentPage =this.currentPage
        this.orderInfo.searchWord = this.searchWord
        sessionStorage.setItem('orderInfo', JSON.stringify(this.orderInfo))
        $(".radiobtn").removeClass('checkRadio');
        $("#" + id).addClass('checkRadio');
        var str = JSON.parse(sessionStorage.getItem('orderInfo'));
        //    if(config_param.isLog)console.log(str);
        if(item.orgSource == 1) {
          if(config_param.isLog)console.log("orgSource--1")
          var param = {
            "flag": "2", //1: 2 : ,
            "insId": item.orgCode, //id  "0863cee2f86c11e4a09e02004c4f4f50"
            "policyNo": this.orderInfo.policyNo, // "600000510132017060110000023253"
            "productCode": "WT_PK061_01", //
            "wesurePolicyNo":this.orderInfo.wesurePolicyNo != null ? this.orderInfo.wesurePolicyNo: this.orderInfo.policyNo,// 
          }
          let response = api.toStoreDetailOrOrder(param);
          response.then(function(res) {
            if(config_param.isLog)console.log(res.data.data);
            window.location.href = res.data.data;
          }).catch(function(err) {
            if(config_param.isLog)console.log(err);
//          window.location.replace("#/common/errorPage");
          })
        } else {
          if(config_param.isLog)console.log('orgSource--0--')
          if(str.lon) {
            setTimeout(function() {
              window.location.href = "#/order/toOrder"
            }, 100)
          }
        }

      },
      // 
      toshopDetail: function(cur,item) {
        if(config_param.isLog)console.log(item);
        var id = cur;
        //  hospitalName hospitalId distance
        this.orderInfo.hospitalId = item.hospitalId;
        this.orderInfo.hospitalName = item.hospitalName;
        this.orderInfo.distance = item.distance;
        this.orderInfo.avgQuality = item.avgQuality;
        this.orderInfo.doctorId = item.doctorId;
        this.orderInfo.contactTel = item.contactTel;
        this.orderInfo.orgAddress = item.hospitalAddr;
        this.orderInfo.orgSource = item.orgSource; // 
        this.orderInfo.orgCode = item.orgCode; // id
        this.orderInfo.orderTime = {}
        this.orderInfo.isToDetail = true;
        this.orderInfo.isSureCheck = false;
        this.orderInfo.currentPage =this.currentPage
        this.orderInfo.searchWord = this.searchWord
        sessionStorage.setItem('orderInfo', JSON.stringify(this.orderInfo))
//      $(".radiobtn").removeClass('checkRadio');
//      $("#" + id).addClass('checkRadio');
        var str = JSON.parse(sessionStorage.getItem('orderInfo'));
        if(item.orgSource == 1) {
          if(config_param.isLog)console.log("orgSource--1")
          var param = {
            "flag": "1", //1: 2 : ,
            "insId": item.orgCode, //
            "policyNo": this.orderInfo.policyNo, // "600000510132017060110000023253"
            "productCode": "WT_PK061_01", //
            "wesurePolicyNo":this.orderInfo.wesurePolicyNo != null ? this.orderInfo.wesurePolicyNo: this.orderInfo.policyNo,// 
          }
          let response = api.toStoreDetailOrOrder(param);
          response.then(function(res) {
            if(config_param.isLog)console.log(res.data.data);
            window.location.href = res.data.data;
          }).catch(function(err) {
            if(config_param.isLog)console.log(err);
//          window.location.replace("#/common/errorPage");
          })
        } else {
          if(config_param.isLog)console.log('orgSource--0--')
          if(str) {
            setTimeout(function() {
              window.location.href = "#/user/shopDetail"}, 100)
          }
        }
      },
      // 
      gotoMap: function(item) {
        var startName = ""; // 
        if(this.orderInfo.localFlag == true) { // 
          var startX = this.localMsg.lon;
          var startY = this.localMsg.lat;
        } else { // 
          var startX = "";
          var startY = "";
        }
        var cityName = this.cityName;
        var addr = item.hospitalAddr;
        var endName = item.hospitalName; // 
        var lon = item.hospitalLongitudeLatitude.split(",")[0]; // 
        var lat = item.hospitalLongitudeLatitude.split(",")[1]; // 
        var dest = {
          "eword": endName,
          "epointx": lon,
          "epointy": lat
        }
        var url = "http://apis.map.qq.com/tools/routeplan/eword=" + endName + "&epointx=" + lon + "&epointy=" + lat + "&sword=" + startName + "&epointx=" + startX + "&epointy=" + startY + "?referer=myapp&key=6IZBZ-JQ4WP-5SMD2-VJKOA-OYGFZ-26F5B"
        var url = "http://apis.map.qq.com/tools/poimarker?type=1&keyword=" + dest.eword +"&center=" + dest.epointy + "," + dest.epointx + "&region=" + cityName + "&radius=10000&key=OB4BZ-D4W3U-B7VVO-4PJWW-6TKDJ-WPB77&referer=myapp"
        var url = "http://apis.map.qq.com/tools/poimarker?type=0&marker=coord:" + dest.epointy + "," + dest.epointx + ";title:" + encodeURIComponent(dest.eword) + ";addr:" + encodeURIComponent(addr) + "&key=OB4BZ-D4W3U-B7VVO-4PJWW-6TKDJ-WPB77&referer=myapp"
        window.location.href = url // 
        return false; // 
      },
      // 
      searchHospital: function() {
        this.isFoucus = false;
        $(".searchbox").removeClass("foucusbox")
        $(".searchbox .searchicon").removeClass("foucusicon")
        $("#"+this.orderInfo.hospitalId).find('.radiobtn').removeClass('checkRadio');
        $(".inputbox").removeClass("foucusinput")
        $("#searchtext").blur(); // 
        this.searchWord = $.trim(this.searchWord); // 
        var histry = JSON.parse(localStorage.getItem("histry")) || []; // 
        if(this.searchWord != "") {
          histry.unshift(this.searchWord); // 
        }
        histry = this.histryUnique(histry); // 
        if(histry.length > 6) histry.pop(); //
        localStorage.setItem("histry", JSON.stringify(histry)); // localStorage
        var param = {
          "retrieveArgs": {
            "cityCode": this.orderInfo.cityId||'110000',
            "currentPage": '1',
            "districtCode": this.orderInfo.districtCode || "",
            "hospitalName": this.searchWord,
            "latiude": this.orderInfo.latiude || "", // , lat: 39.90403
            "longitude": this.orderInfo.longitude || "", //  116.407526
            "provCode": "",
            "rowsPerPage": "300",
            "source":this.orderInfo.source?this.orderInfo.source:''
          }
        }
        if(this.orderInfo.localFlag == true) {
          param.retrieveArgs.rowsPerPage = "300"
        }
        if(this.orderInfo.localFlag == false) {
          param.retrieveArgs.rowsPerPage = "10"
//        param.retrieveArgs.rowsPerPage = "300"
        }
        this.HospitalList(param, true); //  
        this.closeModal(); // 
      },
      // 
      histrySearch: function(keyWord) {
        this.searchWord = keyWord;
        var param = {
          "retrieveArgs": {
            "cityCode": this.orderInfo.cityId||'110000',
            "currentPage": "1",
            "districtCode": this.orderInfo.districtCode,
            "hospitalName": this.searchWord,
            "latiude": this.orderInfo.latiude, // , lat: 39.90403
            "longitude": this.orderInfo.longitude, //  116.407526
            "provCode": "",
            "rowsPerPage": "300",
            "source":this.orderInfo.source?this.orderInfo.source:''
          }
        }
        this.HospitalList(param, true);
        this.closeModal();
      },
      // 
      useHisWord: function(words) {
        this.searchWord = words
        this.searchHospital();
      },
      // 
      clearHistory: function() {
        var histry = JSON.parse(localStorage.getItem("histry"))
        var ary = [1, 2, 3, 4];
        histry.splice(0, histry.length); // 
        localStorage.setItem("histry", JSON.stringify(histry));
        this.histryWord = histry;
//      if(config_param.isLog)console.log(histry); //  []
      },
      // 
      searchFoucus: function() {
        this.isFoucus = true;
        $(".searchbox").addClass("foucusbox")
        $(".searchbox .searchicon").addClass("foucusicon")
        $(".inputbox").addClass("foucusinput")
        $('#searchtext').attr('placeholder', '')
        $('.cancelbtn').show();
        $("body").height($(window).height());
        $(".modal").css({'display': 'block'});
        $(".modal").height($(window).height())
        $("body").css({
          'overflow': "hidden",
          'position': 'fixed',
          'width': '100%'
        })
        var histry = JSON.parse(localStorage.getItem("histry")) || [];
        this.histryWord = histry
      },
      // 
      closeModal: function() {
        $('#searchtext').css({'width': '100%'});
        $('.modal').hide();
        $('.cancelbtn').hide();
        $("body").css({
          'overflow': "auto",
          'position': 'static',
          'width': '100%',
          'height': '100%'
        })
      },
      // 
      histryUnique: function(arr) {
        var res = [];
        var json = {};
        for(var i = 0; i < arr.length; i++) {
          if(!json[arr[i]]) {
            res.push(arr[i]);
            json[arr[i]] = 1;
          }
        }
        return res;
      },
    }
  }
</script>

